import React, { Component } from 'react';
import classes from './navbar.module.css';
import { Link, BrowserRouter } from 'react-router-dom';
import Sidebar from '../sidebar/sidebar';
import Navbar from './navbar';
import ProductList from '../Product/product';

const Navbars = (props) => {
	return (
		<div>
			<header>
				<nav className={classes.navbar}>
				<div className={classes.logo}>
						<Link to='/admin'>
						<h3>product admin</h3>
						</Link>
					</div>

					<ul className={classes.menuItems}>
						<li>
							<Link className={classes.active} to="/admin">
								<i class="fas fa-tachometer-alt" />Dashboard
							</Link>
						</li>
						<li>
							<Link to="/product">
								<i class="fas fa-shopping-cart" />Products
							</Link>
						</li>
						<li>
							<Link to="/account">
								<i class="far fa-user" />Account
							</Link>
						</li>
					</ul>
					<ul className={classes.logout}>
						<li>
							<Link to="/">
								Admin, <b>Logout</b>
							</Link>
						</li>
					</ul>
					<div className={classes.iconbars} onClick={props.burgerbutton}>
						<i class="fas fa-bars" />
					</div>
				</nav>
			</header>
		</div>
	);
};

export default Navbars;
